<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="orderList"
        style="width: 100%"
      >
        <el-table-column type="index">
        </el-table-column>
        <el-table-column
          label=""
          prop="order_number"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="order_price"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="order_pay"
        >
          <template slot-scope="scope">
            <el-tag
              type="success"
              v-if="scope.row.order_pay==='1'"
            ></el-tag>
            <el-tag
              type="danger"
              v-else
            ></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="is_send"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="create_time"
        >
          <template slot-scope="scope">
            {{scope.row.create_time |dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showBox"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgressBox"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressFormClose"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item
          label="///"
          prop="address1"
        >
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="addressProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item
          label=""
          prop="address2"
        >
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addressVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="addressVisible = false"
        > </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressVisible"
      width="50%"
    >
       <el-timeline>
    <el-timeline-item
      v-for="(activity, index) in progressInfo"
      :key="index"
      :timestamp="activity.time">
      {{activity.context}}
    </el-timeline-item>
  </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: 0,
      // 
      orderList: [],
      // 
      addressVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      addressProps: {
        expandTrigger: 'hover',
        label: 'label',
        value: 'value',
        children: 'children'
      },
      // 
      progressVisible: false,
      progressInfo: []
    }
  },
  created() {
    this.getQueryList()
  },
  methods: {
    async getQueryList() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.total = res.data.total
      this.orderList = res.data.goods
    },
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
    },
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getQueryList()
    },
    showBox() {
      this.addressVisible = true
    },
    addressFormClose() {
      this.$refs.addressFormRef.resetFields()
    },
    async showProgressBox() {
      const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      if (res.meta.status !== 200) return this.$message.error('')
      this.progressInfo = res.data
      this.progressVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';
.el-cascader {
  width: 100%;
}
</style>
